import React, { useState } from 'react'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import { sendGridEmail } from 'react-native-sendgrid'

const SENDGRIDAPIKEY = "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";
const FROMEMAIL = "test@test.com";
const TOMEMAIL = "pascaledalkie@gmail.com";
const SUBJECT = "You have a new message";

export default class RegisterForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = { selectValue: "AVeryFashionableTalk", name: "", email: "" };

    this.handleSubmit = this.handleSubmit.bind(this);
  }
 
  handleSubmit(e) {
    e.preventDefault();

    console.log();
    
    const Message = "Event Registration/nEvent: " + this.state.selectValue + " Name: " + this.state.name + " Mail: "+ this.state.email
    
    const sendRequest = sendGridEmail(SENDGRIDAPIKEY, TOMEMAIL, FROMEMAIL, SUBJECT, Message )
        sendRequest.then((response) => {
            console.log("Success")
        }).catch((error) =>{
            console.log(error)
        });
  }

  render() {
      const handleChange = (e) => {
        this.setState({selectValue: e.target.value});
      }
      const setName = (e) => {
        this.setState({name: e.target.value});
      }
      const setEmail = (e) => {
        this.setState({email: e.target.value});
      }
      return (
        <>
          <Form onSubmit={this.handleSubmit}>
            <Form.Group controlId="eventSelect">
              <Form.Label>Select the event you would like to attend</Form.Label>
              <Form.Control required as="select" onChange={handleChange}>
                <option value="AVeryFashionableTalk">A Very Fashionable Talk</option>
                <option value="MakeSewDo">Make, Sew, Do</option>
                <option value="MigrantZineCollective">Migrant Zine Collective</option>
                <option value="MiniatureFashion">Miniature Fashion</option>
                <option value="SwapitLikeItsHot">Swap it Like Itâs Hot</option>
                <option value="Speed-datingSustainableFashionLeaders">Make/Haste: Speed-dating Sustainable Fashion Leaders</option>
                <option value="PrintingFashionAccessories">NZFW: 3D Printing Fashion Accessories</option>
              </Form.Control>
            </Form.Group>
            {this.state.selectValue != "PrintingFashionAccessories" ? (
              <>
              <Form.Group required controlId="eventName">
                <Form.Label>Your name</Form.Label>
                <Form.Control type="email" placeholder="Enter your name" onChange={setName} />
              </Form.Group>
              <Form.Group required controlId="eventEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" placeholder="Enter email" onChange={setEmail} />
              </Form.Group>
              <Button variant="primary" type="submit">
                Submit
              </Button>
              </>
              ) : (
                <p>Google link</p>
              )}
          </Form>

        </>
      )
  }
}